<div class="language-java ext-java line-numbers-mode"><pre v-pre class="language-java"><code><span class="token keyword">public</span> <span class="token keyword">enum</span> <span class="token class-name">ResultCodeEnum</span> <span class="token punctuation">{</span>
    <span class="token doc-comment comment">/*** 通用部分 100 - 599***/</span>
    <span class="token comment">// 成功请求</span>
    <span class="token function">SUCCESS</span><span class="token punctuation">(</span><span class="token number">200</span><span class="token punctuation">,</span> <span class="token string">"successful"</span><span class="token punctuation">)</span><span class="token punctuation">,</span>
    <span class="token comment">// 重定向</span>
    <span class="token function">REDIRECT</span><span class="token punctuation">(</span><span class="token number">301</span><span class="token punctuation">,</span> <span class="token string">"redirect"</span><span class="token punctuation">)</span><span class="token punctuation">,</span>
    <span class="token comment">// 资源未找到</span>
