    <span class="token comment">// 服务器错误</span>
    <span class="token function">SERVER_ERROR</span><span class="token punctuation">(</span><span class="token number">500</span><span class="token punctuation">,</span><span class="token string">"server error"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

		<span class="token comment">//可以根据不同模块用不同的区级分开错误码</span>
    <span class="token comment">// 1000～1999 区间表示用户模块错误</span>
    <span class="token comment">// 2000～2999 区间表示订单模块错误</span>
    <span class="token comment">// 3000～3999 区间表示商品模块错误</span>

    <span class="token doc-comment comment">/**
