<h2 id="api-文档框架-swagger2" tabindex="-1"><a class="header-anchor" href="#api-文档框架-swagger2" aria-hidden="true">#</a> API 文档框架 Swagger2</h2>
<h3 id="swagger2-文档构建工具" tabindex="-1"><a class="header-anchor" href="#swagger2-文档构建工具" aria-hidden="true">#</a> Swagger2 文档构建工具</h3>
<p>前后端分离的开发模式中，前端开发者和后端开发者需要及时地沟通 RESTful API 接口约定，非常耗时费力。</p>
<p>这里借助第三方构建工具 <code>Swagger2</code> 来实现Api文档生成功能 。</p>
<p>这样减少彼此的沟通成本。</p>
<p><strong>引入依赖</strong></p>
