<p>默认组名为 default  ,通过 groupName() 可配置分组</p>
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>@Bean
public Docket createDocket() {
   return new Docket(DocumentationType.SWAGGER_2).apiInfo(apiInfo())
      .groupName("wukong") // 配置分组
       // 省略配置....
}
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><p>配置多个分组</p>
