<blockquote>
<p>编写 login.html 页面进行测试， 或者选择 PostMan 进行测试</p>
</blockquote>
<div class="language-sql ext-sql line-numbers-mode"><pre v-pre class="language-sql"><code><span class="token operator">&lt;</span><span class="token operator">!</span>DOCTYPE html<span class="token operator">></span>
<span class="token operator">&lt;</span>html lang<span class="token operator">=</span><span class="token string">"en"</span><span class="token operator">></span>
<span class="token operator">&lt;</span>head<span class="token operator">></span>
    <span class="token operator">&lt;</span>meta <span class="token keyword">charset</span><span class="token operator">=</span><span class="token string">"UTF-8"</span><span class="token operator">></span>
    <span class="token operator">&lt;</span>title<span class="token operator">></span>登录页面<span class="token operator">&lt;</span><span class="token operator">/</span>title<span class="token operator">></span>
    <span class="token operator">&lt;</span>script src<span class="token operator">=</span><span class="token string">"https://cdn.staticfile.org/jquery/2.0.0/jquery.min.js"</span><span class="token operator">></span>
    <span class="token operator">&lt;</span><span class="token operator">/</span>script<span class="token operator">></span>
    <span class="token operator">&lt;</span>script<span class="token operator">></span>
        $<span class="token punctuation">(</span><span class="token keyword">function</span> <span class="token punctuation">(</span><span class="token punctuation">)</span> {
            $<span class="token punctuation">(</span><span class="token string">"#btn2"</span><span class="token punctuation">)</span><span class="token punctuation">.</span>click<span class="token punctuation">(</span><span class="token keyword">function</span> <span class="token punctuation">(</span><span class="token punctuation">)</span> {
                console<span class="token punctuation">.</span>log<span class="token punctuation">(</span><span class="token string">"输出。。。。"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
            }<span class="token punctuation">)</span><span class="token punctuation">;</span>
            $<span class="token punctuation">(</span><span class="token string">"#btn"</span><span class="token punctuation">)</span><span class="token punctuation">.</span>click<span class="token punctuation">(</span><span class="token keyword">function</span> <span class="token punctuation">(</span><span class="token punctuation">)</span>{
                console<span class="token punctuation">.</span>log<span class="token punctuation">(</span><span class="token string">"提交表单成功"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
                var username <span class="token operator">=</span> $<span class="token punctuation">(</span><span class="token string">"#username"</span><span class="token punctuation">)</span><span class="token punctuation">.</span>val<span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
                var password <span class="token operator">=</span> $<span class="token punctuation">(</span><span class="token string">"#password"</span><span class="token punctuation">)</span><span class="token punctuation">.</span>val<span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
                console<span class="token punctuation">.</span>log<span class="token punctuation">(</span>username<span class="token punctuation">)</span><span class="token punctuation">;</span>
                console<span class="token punctuation">.</span>log<span class="token punctuation">(</span>password<span class="token punctuation">)</span><span class="token punctuation">;</span>
                $<span class="token punctuation">.</span>ajax<span class="token punctuation">(</span>{
                    url:<span class="token string">'http://localhost:8080/api/v1/login.do'</span><span class="token punctuation">,</span>
                    <span class="token keyword">type</span>:<span class="token string">'POST'</span><span class="token punctuation">,</span>
                    <span class="token comment">/*无论 GET 还是 POST ， form 表单的默认 contentType 都为 application/x-www-form-urlencoded  */</span>
                    <span class="token comment">/* GET 时，请求参数放入url路径中； POST 时，请求参数放入请求体 Request Body 中 */</span>
                    contentType:<span class="token string">'application/x-www-form-urlencoded'</span><span class="token punctuation">,</span>
                    <span class="token keyword">data</span>:{
                        <span class="token string">"username"</span>: username<span class="token punctuation">,</span>
                        <span class="token string">"password"</span>: password
                    }<span class="token punctuation">,</span>
                    success:<span class="token keyword">function</span> <span class="token punctuation">(</span>res<span class="token punctuation">)</span> {
                        console<span class="token punctuation">.</span>log<span class="token punctuation">(</span>res<span class="token punctuation">)</span><span class="token punctuation">;</span>
                    }<span class="token punctuation">,</span>
                    error:<span class="token keyword">function</span> <span class="token punctuation">(</span>err<span class="token punctuation">)</span> {
                        console<span class="token punctuation">.</span>log<span class="token punctuation">(</span>err<span class="token punctuation">)</span><span class="token punctuation">;</span>
                    }
                }<span class="token punctuation">)</span><span class="token punctuation">;</span>
            }<span class="token punctuation">)</span><span class="token punctuation">;</span>

        }<span class="token punctuation">)</span><span class="token punctuation">;</span>
    <span class="token operator">&lt;</span><span class="token operator">/</span>script<span class="token operator">></span>
<span class="token operator">&lt;</span><span class="token operator">/</span>head<span class="token operator">></span>
<span class="token operator">&lt;</span>body<span class="token operator">></span>
<span class="token operator">&lt;</span><span class="token operator">div</span><span class="token operator">></span>
    <span class="token operator">&lt;</span><span class="token operator">div</span><span class="token operator">></span>
        <span class="token operator">&lt;</span>label<span class="token operator">></span>
           用户名： <span class="token operator">&lt;</span>input <span class="token keyword">type</span><span class="token operator">=</span><span class="token string">"text"</span> name<span class="token operator">=</span><span class="token string">"username"</span> id<span class="token operator">=</span><span class="token string">"username"</span><span class="token operator">></span>
        <span class="token operator">&lt;</span><span class="token operator">/</span>label<span class="token operator">></span>
        <span class="token operator">&lt;</span>label<span class="token operator">></span>
           密码：  <span class="token operator">&lt;</span>input <span class="token keyword">type</span><span class="token operator">=</span><span class="token string">"text"</span> name<span class="token operator">=</span><span class="token string">"password"</span> id<span class="token operator">=</span><span class="token string">"password"</span><span class="token operator">></span>
        <span class="token operator">&lt;</span><span class="token operator">/</span>label<span class="token operator">></span>
       <span class="token operator">&lt;</span><span class="token operator">!</span><span class="token comment">-- &lt;input id="btn" type="submit" value="登录">--></span>
        <span class="token operator">&lt;</span>button id<span class="token operator">=</span><span class="token string">"btn"</span><span class="token operator">></span>登录<span class="token operator">&lt;</span><span class="token operator">/</span>button<span class="token operator">></span>
    <span class="token operator">&lt;</span><span class="token operator">/</span><span class="token operator">div</span><span class="token operator">></span>
<span class="token operator">&lt;</span><span class="token operator">/</span><span class="token operator">div</span><span class="token operator">></span>
<span class="token operator">&lt;</span><span class="token operator">div</span><span class="token operator">></span>
   
<span class="token operator">&lt;</span><span class="token operator">/</span><span class="token operator">div</span><span class="token operator">></span>
